import * as Firebase from '../lib/firebase'

export const contractAddress = '0x60f80121c31a0d46b5279700f9df786054aa5ee5'

export type Token = {
  tokenId: number
  imageUrl: string
}

const collection = Firebase.firestore.collection('nft_tokens')

export async function index() {
  const snapshot = await collection.get()
  const tokens = snapshot.docs.map(doc => doc.data() as Token)
  return tokens
}