import React from 'react'
import RedeemForm from '../models/redeem_form'

type Props = {
  model: RedeemForm
  onSubmit: (model: RedeemForm) => any
  onClose: () => any
}
type State = {
  model: RedeemForm
}

class RedeemFormConponent extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props)
    this.state = {
      model: props.model,
    }
    this.onChange = this.onChange.bind(this)
    this.onSubmit = this.onSubmit.bind(this)
  }

  render() {
    const { model } = this.props
    
    return (
      <>
        <h1>Redeem</h1>
        <form onSubmit={this.onSubmit}>
          <input
            type="text" name="country" placeholder="country" value={model.country}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              model.country = event.currentTarget.value
              this.onChange(model)
            }}
          />
          <br/>
          <input
            type="text" name="city" placeholder="city" value={model.city}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              model.city = event.currentTarget.value
              this.onChange(model)
            }}
          />
          <br/>
          <input
            type="text" name="address" placeholder="address" value={model.address}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
              model.address = event.currentTarget.value
              this.onChange(model)
            }}
          />
          <br/>
          <button type="submit">Submit</button>
          <button onClick={this.props.onClose}>Close</button>
        </form>
      </>)
  }

  private onChange(model: RedeemForm) {
    const clone = Object.assign({}, model) as RedeemForm
    this.setState({
      model: clone
    })
  }

  private onSubmit(event: React.SyntheticEvent<HTMLFormElement>) {
    event.preventDefault()
    this.props.onSubmit(this.state.model)
  }
}

export default RedeemFormConponent