import React from 'react'

import * as NFT from '../nft'
import * as NFTToken from '../models/nft_token'
import * as RedeemForm from '../models/redeem_form'

import CSSImage from './css_image'

import { useWeb3React } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'
import RedeemFormComponent from './redeem_form'

type Owners = {[key: number]: string} // token id => owner

const Showcase: React.FC<{}> = ({children}) => {
  const { account, library } = useWeb3React<Web3Provider>()

  const [owners, setOwners] = React.useState<Owners | undefined>(undefined)
  const [redeemedTokens, setRedeemedTokens] = React.useState<number[] | undefined>(undefined)
  const [tokens, setTokens] = React.useState<NFTToken.Token[] | undefined>(undefined)
  const [redeemForm, setRedeemForm] = React.useState<RedeemForm.RedeemForm | undefined>(undefined)

  // Load tokens from Firebase
  React.useEffect(() => {
    if (!!tokens) { return } // guard: already done

    (async () => {
      const tokens = await NFTToken.index()
      setTokens(tokens)
    })()
  }, [tokens])

  // Load redeemed tokens
  React.useEffect(() => {
    if (!account) { return } // guard: web3
    if (!!redeemedTokens) { return } // guard: already done

    (async () => {
      const redeemedTokens = await RedeemForm.getRedeemedTokenIds(account)
      setRedeemedTokens(redeemedTokens)
    })()
  }, [account, redeemedTokens])

  // Get token owners
  React.useEffect(() => {
    if (!account || !library) { return } // guard: web3
    if (!tokens) { return } // guard: tokens to be fetched
    if (!!owners) { return } // guard: already done
    
    (async () => {
      const signer = await library.getSigner(account)
      const contract = NFT.getEIP721Contract(NFTToken.contractAddress, signer)

      const clone = Object.assign({}, owners) as Owners

      for(const token of tokens) {
        try {
          const ownerOf = await (contract as any).ownerOf(token.tokenId)
          clone[token.tokenId] = ownerOf
        } catch (err) {
          console.error(err)
        }
      }

      setOwners(clone)
    })()
  }, [account, library, owners, tokens])

  const didOwnersFetched = !!owners

  return (
    <>
      { !tokens ?
          <>‚åõ Loading</> :
          <ul>
            { tokens!.map(token => {
              const ownerOf = (owners || {})[token.tokenId]
              const isOwner = !!ownerOf && ownerOf === account
              const isRedeemed = isOwner && (redeemedTokens || []).includes(token.tokenId)
              return (
                <li key={`${token.tokenId}`}>
                  <CSSImage src={token.imageUrl} width={150} height={150}/>
                  <br/>
                  {token.tokenId}
                  <br/>
                  {
                    !didOwnersFetched ?
                      <>‚åõ</> : (
                        !ownerOf ?
                          <a href="https://opensea.io/" target="_blank" rel="noreferrer">Buy</a> :
                          <>{ isOwner ?
                                (
                                  isRedeemed ? 
                                    <>Already redeemed</> :
                                    <>‚úÖ You can <button onClick={() => {
                                      setRedeemForm({ owner: account!, tokenId: token.tokenId })
                                    }}>redeem</button> your T-shirt</>
                                ) : 
                                <>Sold</> }</>
                      )
                  }
                </li>)
            })}
          </ul>
      }
      { !!redeemForm && (
        <RedeemFormComponent
          model={redeemForm}
          onSubmit={async (m) => {
            await RedeemForm.create(m)
            setRedeemedTokens([...(redeemedTokens!), m.tokenId])
            setRedeemForm(undefined)
          }}
          onClose={() => setRedeemForm(undefined)}
        />
      ) }
    </>)
}

export default Showcase