import React from 'react'

type Props = {
  src: string
  width: number
  height: number
}

const CssImage: React.FC<Props> = (props: Props) => {
  return (
    <div
      style={{
        width: props.width,
        height: props.height,
        backgroundImage: `url(${props.src})`,
        backgroundSize: "contain",
        backgroundRepeat: "no-repeat",
      }}
    />
  )
}

export default CssImage